'use strict';
export const todo = () => {
    const inputText = document.querySelector('.todo__input');
    const buttonAdd = document.querySelector('.button-add');
    const buttonSort = document.querySelectorAll('.sort__button');
    const todoContainer = document.querySelector('.todo__list');
    const buttonRemove = document.querySelector('.button-remove');
    let infoItem = document.querySelector('.info__item');
    let todoList = [];
    const audioDone =  new Audio();
          audioDone.src="audio/done.mp3";
    const key = {
        enter: 13
    };

    class Todo {
        constructor(text, status) {
            this.text = text;
            this.status = status;
            this.id = this.getId()
        }

        getId() {
            return '_' + Math.random().toString(36).substr(2, 9);
        }

        render(contNode = document.querySelector('.todo__list')) {
            const container = contNode;
            container.insertBefore(this.templete(), contNode.firstChild);


        }

        templete() {
            let todoItem = document.createElement('li');
            let buttonDelet = document.createElement('button');
            buttonDelet.classList.add('delet');
            todoItem.textContent = this.text;
            todoItem.appendChild(buttonDelet);
            todoItem.classList.add('todo__item');
            if (!this.status) {
                todoItem.classList.add('close');
            }

            this.eventClick(todoItem);
            return todoItem
        }

        eventClick(item) {
            const buttonDelet = item.querySelector('button');
            let that = this;
            let itemID = that.id;
            item.addEventListener('click', function (e) {
                if (!item.classList.contains('close')) {
                    that.status = false;
                    e.currentTarget.classList.add('close');
                    audioDone.play()
                } else {
                    that.status = true;
                    e.currentTarget.classList.remove('close');
                }

            });
            buttonDelet.addEventListener('click', function (e) {
                const parent = e.currentTarget.parentElement;
                parent.parentNode.removeChild(parent);
                deletTask(itemID, todoList);
            })
        }

    }

    const storageUser = {
        getStoreg: function (name) {
            return JSON.parse(localStorage.getItem('"' + name + '"'))
        },
        setStoreg: function (name, data) {
            localStorage.setItem('"' + name + '"', JSON.stringify(data));
        },
        removeStoreg: function (name) {
            localStorage.removeItem('"' + name + '"');
        }

    };

const checkid = (pamID , arry )=>{
        let id = pamID;
        return arry.findIndex(function checkid(element) {
            return element.id === id;
        });
    };
    const deletTask = (pamID, arry) => {
        let numbIndex = checkid(pamID, arry);
        todoList.splice(todoList[numbIndex], 1);
        counterTask();
    };

    const addingAtask = (task, status = true) => {
        let newTask;
        if (task.trim()) {
            newTask = new Todo(task, status);
            newTask.render();
            todoList.push(newTask);
            counterTask();
        }
        inputText.value = "";
    };
    inputText.addEventListener('keydown', (e) => {
        if (e.keyCode === key.enter) {
            addingAtask(e.currentTarget.value);
        }
    });
    buttonAdd.addEventListener('click', () => {
        addingAtask(inputText.value);
    });

    buttonSort.forEach(function (item) {
        item.addEventListener('click', function (e) {
            if (!e.currentTarget.classList.contains('active')) {
                for (let i = 0; i < buttonSort.length; i++) {
                    buttonSort[i].classList.remove('active')
                }
                e.currentTarget.classList.add('active')
            }

            if (e.currentTarget.classList.contains('sort__button--active')) {
                todoFilter(todoList, true)
            }
            if (e.currentTarget.classList.contains('sort__button--close')) {
                todoFilter(todoList, false)
            }
            if (e.currentTarget.classList.contains('sort__button-all')) {
                todoFilter(todoList)
            }

        });
    });

    const counterTask = (number = todoList.length) => {
        infoItem.textContent = "Количество задач :" + " " + number
    };

    const cleaning = () => {
        todoList.splice(0, todoList.length);
        counterTask();
        infoItem.textContent = "Количество задач : 0";
        while (todoContainer.children.length) {
            todoContainer.removeChild(todoContainer.lastChild);
        }
        storageUser.removeStoreg('userTask')

    };

    buttonRemove.addEventListener('click', function () {
        cleaning()
    });


    const todoFilter = (arr, status) => {
        while (todoContainer.children.length) {
            todoContainer.removeChild(todoContainer.lastChild);
        }
        if (status === undefined) {
            arr.forEach(function (item) {
                item.render()
            })
        } else {
            let result = arr.filter(todoItem => todoItem.status === status);

            result.forEach(function (item) {
                item.render()
            });

            counterTask(result.length);
        }
    };






    const load = (arry) => {
        if (!arry) {
            return
        } else {
            for (let i = 0; i < arry.length; i++) {
                addingAtask(arry[i].text, arry[i].status)
            }
        }


    };

    load(storageUser.getStoreg('userTask'));

    window.addEventListener("beforeunload", function(event) {
        storageUser.setStoreg('userTask', todoList);
    });
};

